export default "<template>\n  <div>\n    <f-bottom-sheet\n      v-model=\"show\"\n      :persistent=\"persistent\"\n      :adaptive=\"adaptive\"\n      :wapper-in-desktop=\"desktopWapper\"\n      title=\"选择一个人物\"\n      content-class=\"pepole\"\n    >\n      <template #activator=\"{ on }\">\n        <f-button color=\"primary\" block v-on=\"on\"> Show selector </f-button>\n      </template>\n      <f-bottom-sheet-subtitle>\n        <f-input v-model=\"filter\" label=\"search\"></f-input>\n      </f-bottom-sheet-subtitle>\n      <v-list height=\"400\" style=\"overflow: scroll\">\n        <v-list-item v-for=\"(item, index) in filtedItems\" :key=\"index\">\n          <v-list-item-icon>\n            <v-icon v-if=\"item.icon\" color=\"pink\">mdi-star</v-icon>\n          </v-list-item-icon>\n\n          <v-list-item-content>\n            <v-list-item-title v-text=\"item.title\"></v-list-item-title>\n          </v-list-item-content>\n\n          <v-list-item-avatar>\n            <v-img :src=\"item.avatar\"></v-img>\n          </v-list-item-avatar>\n        </v-list-item>\n      </v-list>\n      <div class=\"px-5 mt-5\">\n        <v-btn block rounded depressed color=\"primary\" @click=\"handleClose\">\n          OK\n        </v-btn>\n      </div>\n    </f-bottom-sheet>\n\n    <v-row>\n      <v-col cols=\"6\" class=\"d-flex justify-center\">\n        <v-switch v-model=\"persistent\" label=\"Persistent\" />\n      </v-col>\n      <v-col col=\"6\" class=\"d-flex justify-center\">\n        <v-switch v-model=\"adaptive\" label=\"Adaptive\" />\n      </v-col>\n    </v-row>\n    <v-row>\n      <v-col cols=\"6\" class=\"d-flex justify-center\">\n        <v-select\n          v-model=\"desktopWapper\"\n          label=\"Wapper in desktop\"\n          :items=\"desktopWappres\"\n        />\n      </v-col>\n    </v-row>\n  </div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue } from \"vue-property-decorator\";\n\n@Component\nclass BottomSheet extends Vue {\n  show = false;\n\n  filter = \"\";\n\n  persistent = false;\n\n  adaptive = true;\n\n  desktopWapper = \"menu\";\n\n  desktopWappres = [\n    { text: \"Menu\", value: \"menu\" },\n    { text: \"Dialog\", value: \"dialog\" }\n  ];\n\n  items = [\n    {\n      icon: true,\n      title: \"Jason Oner\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/1.jpg\"\n    },\n    {\n      title: \"Travis Howard\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/2.jpg\"\n    },\n    {\n      title: \"Ali Connors\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/3.jpg\"\n    },\n    {\n      title: \"Cindy Baker\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/4.jpg\"\n    },\n    {\n      icon: true,\n      title: \"Jason Oner\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/1.jpg\"\n    },\n    {\n      title: \"Travis Howard\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/2.jpg\"\n    },\n    {\n      title: \"Ali Connors\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/3.jpg\"\n    },\n    {\n      title: \"Cindy Baker\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/4.jpg\"\n    },\n    {\n      icon: true,\n      title: \"Jason Oner\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/1.jpg\"\n    },\n    {\n      title: \"Travis Howard\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/2.jpg\"\n    },\n    {\n      title: \"Ali Connors\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/3.jpg\"\n    },\n    {\n      title: \"Cindy Baker\",\n      avatar: \"https://cdn.vuetifyjs.com/images/lists/4.jpg\"\n    }\n  ];\n\n  get filtedItems() {\n    return this.items.filter((item) => item.title.includes(this.filter));\n  }\n\n  handleClose() {\n    this.show = false;\n  }\n}\n\nexport default BottomSheet;\n</script>\n<style lang=\"scss\" scoped></style>\n";